/* Custom variables */
:root {
  --background-color: #69B223;
}

/* Custom fonts */
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  src: local('Roboto'), local('Roboto-Regular'), url(/react-redux-todo-app/static/media/Roboto-Regular.3e1af3ef.ttf) format('ttf'),
    url(/react-redux-todo-app/static/media/Roboto-Regular.3e1af3ef.ttf) format('ttf');
}

@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  src: local('Roboto Bold'), local('Roboto-Bold'), url(/react-redux-todo-app/static/media/Roboto-Bold.ee7b96fa.ttf) format('ttf'),
    url(/react-redux-todo-app/static/media/Roboto-Bold.ee7b96fa.ttf) format('ttf');
}

/* General */
html {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  width: 100%;
  height: 100%;
}

#app {
  width: 100%;
  height: 100%;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.app__container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 100%;
  height: 100%;
}

.app__header {
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  width: 100%;
  height: 10vh;
}

.app__content {
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
  width: 100%;
  margin-top: 1em;
}

.app__footer {
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  width: 100%;
  height: 10vh;
}

.header__container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: 100%;
  background: var(--background-color);
}

.header__span {
  margin: 0 0.2em;
  vertical-align: super;
  font-size: 1.3em;
  color: #fff;
}

.form_edit__component {
  width: 100%;
  margin-bottom: 0.5em;
}

.form_edit__button--first {
  margin-right: 0.5em;
}

.form_edit__input {
  width: 100%;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.form_item__component {
  width: 100%;
  height: 100%;
  position: relative;
  cursor: pointer;
  margin-bottom: 0.5em;
}

.form_item__component:hover {
  background: var(--background-color);
  color: #fff;
}

.form_item__checkout {
  margin-right: 0.5em;
  height: 100%;
}

.form_item__text {
  vertical-align: super;
}

.property_bar__component {
  position: absolute;
  right: 1em;
  top: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  height: 100%;
}

.property_bar__button {
  margin-right: 0.5em;
  opacity: 0.1;
  -webkit-transition: opacity 0.25s ease-in-out;
  transition: opacity 0.25s ease-in-out;
}

.property_bar__button:hover {
  opacity: 1;
  -webkit-transition: opacity 0.25s ease-in-out;
  transition: opacity 0.25s ease-in-out;
}

.content__todos {
  margin: 1em 0 1em 0;
}

.content__todos__li {
  opacity: 0;
  margin-bottom: 0.5em;
  -webkit-animation: fadeIn 0.3s 1;
          animation: fadeIn 0.3s 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.footer__container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: 100%;
  background: var(--background-color);
}

.footer__p {
  margin: 0;
  color: #fff;
}

